<template>
  <div id="wrapper">
    <div class="block">
      <el-carousel height="6.25rem">
        <el-carousel-item v-for="item in items">
          <img :src='item' />
        </el-carousel-item>
      </el-carousel>
    </div>
  </div>
</template>
<style>

.footer-image {
  padding-top: 13px;
}
.footer-image img {
    width: 100%;
}
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 6.25rem;
    margin: 0;
    width: 100%;
  }
  .el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }
  .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  }
</style>

<script>
export default {
  data() {
    return {
      items:[
        "http://mp1.jmstatic.com/c_zoom,w_640,q_80/mobile/card_material/item_0_2048_768-ipad2048_58789fb6ccd1a.jpg?t=1484300215",
        "http://mp1.jmstatic.com/c_zoom,w_640,q_80//jmstore/image/000/000/733_std/5876ec074b34b_2048_847.jpg?1484275616"
      ]
    };
  },
  mounted(){
    //loading history
    this.$store.commit('changeMenu', true);
    this.$store.commit('changeNav', 'home');
    this.$store.commit('changeTitle', '故与事');
  },
}
</script>



